.navBar__button {
    width: 228px;
    height: 50px;
    background: #060606;
    border: 1px solid;
    border-radius: 100px;
    color: #ffffff;
    font-family: FordAntenna;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
}
.navBar__button:hover {
    background: linear-gradient(230deg, #FFD916, #FF1914, #6705FF);
}


@media screen and (max-device-width: 1200px) {
    .navBarComponent {
        display: none;
    }

    .navBarMobile {
        width: 320px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .navBar__mobileLogo {
        display: block;
        background-image: url(/public/UI/mobileLogo.svg);
        width: 177px;
        height: 34px;
        margin-left: 20px;
    }

    .navBar__burger {
        margin-right: 20px;
        background-image: url(/public/UI/burger.svg);
        width: 30px;
        height: 30px;
        position: fixed;
        right: 0;
        z-index: 1;
    }
}